<template>
  <div id="app">
    <el-row>
      <h1>환율 계산</h1>
    </el-row>
    <el-row>
      <h3>송금국가: 미국(USD)</h3>
    </el-row>
    <el-row type="flex" justify="center">
        <h3 class="vertical-center-box">수취국가:
          <el-select v-model="selected" @change="onChange" sytle="width:70px;">
            <el-option v-for="item in nationList" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </h3>
    </el-row>
    <el-row>
      <h3>환율: {{ exchangeRate.toLocaleString('ko-KR') }} {{ currency }}</h3>
    </el-row>
    <el-row type="flex" justify="center">
      <h3 class="vertical-center-box">송금액:
        <el-input v-model="remittanceAmount" type="number"></el-input> USD
      </h3>
    </el-row>
    <el-row>
      <el-button @click="submit">Submit</el-button>
    </el-row>
    <el-row>
      <h3 v-if="visible && isTrue">수취금액은 {{ result.toLocaleString('ko-KR') }} {{ currency }} 입니다.</h3>
      <h3 v-if="visible && !isTrue" style="color: red">송금액이 바르지 않습니다</h3>
    </el-row>
  </div>
</template>

<script>
import { isNil, round } from 'lodash';

import { getCurrencyInfo } from '../src/api/common';

export default {
  name: 'App',
  data() {
    return {
      result: 0,
      visible: false,
      isTrue: true,
      selected: '',
      nationList: [
        {
          label: '한국(KRW)',
          value: 'KRW',
        },
        {
          label: '일본(JPY)',
          value: 'JPY',
        },
        {
          label: '필리핀(PHP)',
          value: 'PHP',
        },
      ],
      exchangeRate: 0,
      currency: '',
      remittanceAmount: 0,
      currencyInfo: {},
      message: '',
    };
  },
  mounted() {
    this.selected = this.nationList[0].value;
  },
  watch: {
    selected(val) {
      if (isNil(val)) {
        return;
      }
      this.currency = val;
      this.loadCurrencyInfo();
    },
    currencyInfo(val) {
      if (isNil(val)) {
        return;
      }
      
      const { quotes } = this.currencyInfo;
      if (!isNil(quotes)) {
        this.getCurrency(quotes);
      }
    },
  },
  methods: {
    submit() {
      const isError = this.remittanceAmount < 0 || isNaN(this.remittanceAmount) || this.remittanceAmount > 10000;

      this.visible = true;
      this.isTrue = !isError;

      if (!isError) {
        this.result = round(this.remittanceAmount * this.exchangeRate, 2);
      }
    },
    onChange() {
      this.loadCurrencyInfo();
      this.visible = false;
    },
    getCurrency(quotes) {
      switch (this.currency) {
        case 'KRW':
          this.currency = 'KRW/USD';
          this.exchangeRate = round(quotes.USDKRW, 2);
          break;
        case 'JPY':
          this.currency = 'JPY/USD';
          this.exchangeRate = round(quotes.USDJPY, 2);
          break;
        case 'PHP':
          this.currency = 'PHP/USD';
          this.exchangeRate = round(quotes.USDPHP, 2);
          break;
        default:
          break;
      }
    },
    loadCurrencyInfo() {
      getCurrencyInfo()
        .then((res) => {
          if (!res.data.success) {
            return;
          }
          this.currencyInfo = !isNil(res.data.data) ? res.data.data : {};
        })
        .catch((e) => {
          throw new Error(e);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;

  h1 {
    margin-bottom: 70px;
  }
}

.el-input {
  position: relative;
    font-size: 14px;
    display: inline-block;
    width: 100px;
}

.vertical-center-box {
  display: flex;
  align-items: center;
}
</style>



